export default {
	data() {
		return {
			num:3
		}
	},
	methods: {
		time(){
			let times = setInterval(()=>{
				this.num--
				if(this.num==0){
					clearInterval(times)
					this.num = 3
					uni.switchTab({
						url:"/pages/index/index"
					})
				}
			},1000)
		}
	},
	onLoad(){
		const _this = this
		_this.time()
	}
}
